@import url(https://fonts.googleapis.com/css?family=Quicksand);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Quicksand', sans-serif;
  background: #F1F1F1;
  color: #37474F;
}

.App {
  width: 550px;
  min-width: 400px;
  margin: 20px auto;
}

.Heading {
  width: 550px;
  font-size: 7rem;
  font-weight: normal;
  color: #edd7d3;
  text-align: center;
}

.TodoForm {
  width: 550px;
  height: 70px;
  border: 1px solid lightgrey;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  background: white;
  box-shadow: 6px 6px 10px grey;
}

.TodoFormTextBox {
  font-size: 2rem;
  height: 100%;
  width: 450px;
  border: none;
  background: inherit;
  outline: none;
  margin: 0 10px;
}

.TodoForm-TextBox::-webkit-input-placeholder {
  color: #d1d1d1;
  font-size: 80%;
}

.TodoForm-TextBox:-ms-input-placeholder {
  color: #d1d1d1;
  font-size: 80%;
}

.TodoForm-TextBox::-ms-input-placeholder {
  color: #d1d1d1;
  font-size: 80%;
}

.TodoForm-TextBox::placeholder {
  color: #d1d1d1;
  font-size: 80%;
}

.Toggler {
  font-size: 1.8rem;
  padding: 2px 8px;
  outline: none;
  border: none;
  background: inherit;
  color: grey;
}


.CheckBox input[type=checkbox] {
  height: 40px;
  width: 40px;
  margin-top: 8px;
}

.TodoBox {
  width: 550px;
  height: 70px;
  background: white;
  border: 1px solid lightgrey;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 10px;
  font-size: 1.6rem;
  box-shadow: 6px 6px 10px grey;

  p {
    text-align: left;
    width: 100%;
    padding: 0 10px;
  }
}

.Deleter {
  /*background: #d14949;*/
  background: none;
  border: none;
  padding: 10px 12px 10px 20px;
  /*color: white;*/
}

.Deleter:hover {
  color: red;
  font-weight: 700;
  font-size: 14px;
}

.Footer {
  width: 550px;
  height: 50px;
  background: white;
  border: 1px solid lightgrey;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 10px;
  box-shadow: 6px 6px 10px grey;
}
